import { Grid, Typography, Box } from "@mui/material";
import { Table, TableHead, TableBody, TableCell, TableRow, TableContainer } from '@mui/material';

import { getWeatherData, getWeatherParams } from "./currentData";

const CurrentView = ({ currentdata }) => {
  const [ weatherData, lastupd, cityName, localData, localTime] = currentdata;
  const { current } = weatherData
  const { humidity, clouds, uvi } = current;
  const { descr, icon } = getWeatherData(current.weather);
  const {
    newPressure,
    newTemp,
    newFeels,
    newDewPoint,
    newWindSpeed,
    newWindDirect,
    newVisibility,
    newSunrise,
    newSunset,
    dayDuration,
  } = getWeatherParams(weatherData);

  
  const currentItem_1 = [
    { name: " : ", value: localTime },
    { name: ": ", value: newSunrise },
    { name: ": ", value: newSunset },
    { name: " : ", value: dayDuration },
  ];

  const currentItem_2 = [
    { name: ": ", value: newTemp + " " },
    { name: " : ", value: newFeels + " " },
    { name: ": ", value: humidity + " %" },
    { name: ": ", value: newPressure + "   " },
    { name: " : ", value: newDewPoint + " " },
  ];

  const currentItem_3 = [
    {
      name: " : ",
      value: newWindSpeed ? newWindSpeed + " /" : "",
    },
    { name: ": ", value: newWindDirect },
    { name: ": ", value: clouds ? clouds + " %" : "" },
    { name: ": ", value: newVisibility + " " },
    { name: "UV : ", value: uvi ? uvi.toFixed(1) : uvi },
  ];

  return (
    <Box className="current_view">
      <Typography className="last_upd"> : {lastupd}</Typography>
      <Grid container>
        <Grid item xs={12} md={6} xl={3}>
          <Box className="city_box">
            <Typography className='city_name'>{cityName}</Typography>
            <img
              src={`https://openweathermap.org/img/wn/${icon}@2x.png`}
              alt={icon}
            />
            <Typography className='city_name'>{descr}</Typography>
          </Box>
        </Grid>
        <Grid item xs={12} md={6} xl={3}>
          <TableContainer className="table">
            <Table>
              <TableHead>
                <TableRow>                
                  <TableCell colSpan={2} className='table_row'>
                    {localData}
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {currentItem_1.map((item, i) => {
                  return (
                    <TableRow key={i}>
                      <TableCell className='table_cell_item'>{item.name}</TableCell>
                      <TableCell className='table_cell_item item_2'>{item.value}</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        <Grid item xs={12} md={6} xl={3}>
          <TableContainer className="table">
            <Table>
              <TableBody>
                {currentItem_2.map((item, i) => {
                  return (
                    <TableRow key={i}>
                      <TableCell className='table_cell_item'>{item.name}</TableCell>
                      <TableCell className='table_cell_item item_2'>{item.value}</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        <Grid item xs={12} md={6} xl={3}>
        <TableContainer className="table">
            <Table>
              <TableBody>
                {currentItem_3.map((item, i) => {
                  return (
                    <TableRow key={i}>
                      <TableCell className='table_cell_item'>{item.name}</TableCell>
                      <TableCell className='table_cell_item item_2'>{item.value}</TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
      </Grid>
    </Box>
  );
};

export default CurrentView;
